.project-section {
  width: 100%;
  height: 100%;
}
.project-section .heading h1 {
  background-color: rgb(233, 233, 225);
  padding: 1rem 0;
  align-items: center;
  text-align: center;
  color: rgb(225, 169, 66);
  text-transform: uppercase;
}
.grid-three-column {
  grid-template-columns: repeat(3, 1fr);
}
.grid-four-column {
  grid-template-columns: repeat(4, 1fr);
}
.project-section {
  transition: all 0.7s linear;
}
.portfolio-images {
  gap: 3.2rem;
  margin-top: 6rem;
  margin-bottom: 6rem;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
}
.portfolio-images img {
  width: 100%;
  border-radius: 7px;
  position: relative;
  overflow: hidden;
}
.img-overlay {
  position: relative;
  overflow: hidden;
}
.img-overlay .overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 97%;
  background: var(--primary-light);
  opacity: 0;
  display: flex;
  flex-direction: column;
  text-align: center;
  transform: translateY(100%);
  transition: all 0.3s linear;
  border-radius: 7px;
}
.img-overlay:hover > .overlay {
  transform: translateY(0);
  opacity: 0.7;
  cursor: pointer;
}
.overlay h4 {
  background-color: #030303;
  padding: 0.8rem 0;
  color: #322f2f;
  font-weight: 700;
  color: #fff;
  border-radius: 7px;
}
.demo-link {
  margin-top: 1rem;
}
.overlay a {
  padding: 0.5rem 1rem;
  background-color: black;
  color: white;
  border: 1px solid black;
  border-radius: 7px;
}
.overlay a:hover {
  background-color: transparent;
  color: rgb(0, 0, 0);
}
.overlay p {
  padding-top: 0.7rem;
  font-weight: 600;
  font-size: 16px;
  color: black;
}
@media screen and (max-width: 940px) {
  .portfolio-images {
    grid-template-columns: 1fr;
  }
}
