.footer {
  padding: 4rem 0;
  text-align: center;
  font-size: 0.9rem;
  background-color: #0a192f;
}
.footer-logo {
  font-size: 2rem;
  font-weight: 500;
  margin-bottom: 2rem;
  display: inline-block;
  margin-top: 3rem;
  color: #fff;
}
.span-tag {
  color: rgb(225, 22, 22);
}
.footer-links {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 2rem;
  margin: 0 auto 3rem;
}
.footer-links a {
  color: #fff;
}

.footer-social {
  display: flex;
  justify-content: center;
  gap: 1rem;
  margin-bottom: 3rem;
}
.footer-social a {
  background-color: rgb(38, 33, 33);
  color: #fff;
  padding: 0.8rem;
  border-radius: 0.7rem;
  display: flex;
  border: 1px solid transparent;
}
.footer-social a:hover {
  background: transparent;
  color: red;
  border-color: red;
}
.footer-copyright {
  margin-bottom: 3rem;
  color: #fff;
}
@media screen and (max-width: 940px) {
  .footer-links {
    flex-direction: column;
    gap: 1.5rem;
  }
  .footer-social {
    margin-bottom: 2.6rem;
  }
}
