.contact {
  width: 100%;
  height: 100%;
}
.contact h5 {
  justify-content: center;
  align-items: center;
  text-align: center;
  padding-top: 3rem;
  color: black;
  font-size: 16px;
}
.contact h2 {
  padding: 1rem 0;
  background-color: rgb(233, 233, 225);
  justify-content: center;
  align-items: center;
  text-align: center;
  margin-bottom: 3rem;
  color: black;
  font-size: 32px;
  text-transform: uppercase;
  color: rgb(225, 169, 66);
}
.contact-container {
  width: 58%;
  display: grid;
  grid-template-columns: 30% 58%;
  gap: 12%;
  padding: 2rem 0;
}
.contact-options {
  display: flex;
  flex-direction: column;
  gap: 1.2rem;
}
.contact-option {
  background-color: rgb(102, 102, 232);
  padding: 1.2rem;
  border-radius: 1.2rem;
  text-align: center;
  border: 1px solid transparent;
  transition: rgb(102, 102, 232);
}
/* .contact-option:hover {
  background: transparent;
  border-color: rgb(103, 124, 244);
} */
.contact-icon {
  font-size: 1.5rem;
  margin-bottom: 0.5rem;
}
.contact-option a {
  margin-top: 0.7rem;
  display: inline-block;
  font-size: 0.8rem;
}
.contact-option a:hover {
  color: black;
}

form {
  display: flex;
  flex-direction: column;
  gap: 1.2rem;
}
input,
textarea {
  width: 100%;
  padding: 1.5rem;
  border-radius: 0.5rem;
  background: transparent;
  border: 2px solid rgb(113, 110, 110);
  resize: none;
  color: black;
}
.form-btn {
  width: 30%;
}

.form-btn:hover {
  background: transparent;
  color: black;
}

@media screen and (max-width: 940px) {
  .contact-container {
    grid-template-columns: 1fr;
    gap: 2rem;
  }
  .form-btn {
    width: 60%;
  }
}
