.navbar {
  width: 100%;
  height: 12vh;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 1rem;
  z-index: 2;
  color: white;
  background-color: #0a192f;
  position: sticky;
  top: 0;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.3);
}
.logo h2 {
  padding-left: 3rem;
}

.span {
  color: rgb(229, 15, 15);
}
.navbar-menu {
  margin-right: 1rem;
  display: flex;
  padding: 0 1rem;
}
.navbar-menu li {
  position: relative;
}
li {
  padding: 0rem 1rem;
  cursor: pointer;
}
.navbar-menu li a {
  color: white;
  transition: 0.3s ease-in-out;
  font-size: 18px;
}
.navbar-menu li a:hover,
.navbar-menu li a.active {
  color: #17cf97;
}
.navbar-menu li a:hover::after,
.navbar-menu li a.active::after {
  content: "";
  width: 30%;
  height: 2px;
  position: absolute;
  background-color: #17cf97;
  left: 20px;
  bottom: -2px;
}

.hamburger {
  display: none;
}
.mobile-menu {
  display: none;
}

@media screen and (max-width: 940px) {
  .navbar-menu {
    display: none;
  }
  .hamburger {
    display: block;
    padding-right: 1rem;
  }
  .navbar {
    position: absolute;
  }

  .mobile-menu {
    display: flex;
    flex-direction: column;
    width: 100%;
    top: -100vh;
    left: 0;
    position: absolute;
    justify-content: space-between;
    color: black;
    background-color: white;
    transition: 0.3s;
  }
  .active {
    top: 90px;
  }
  .mobile-menu li a {
    color: black;
  }
  .mobile-menu li {
    padding: 1.2rem 1rem;
    margin: 0 1rem;
    border-bottom: 1px solid #ccc;
  }
  .mobile-nav {
    margin: 1rem 0;
  }
  .mobile-menu-bottom {
    width: 100%;
    text-align: center;
    padding: 1rem 0;
  }
  .social-icons {
    display: flex;
    justify-content: space-around;
    margin: 2rem 0;
  }
  .social-icons .icon:hover {
    transition: 0.3s;
    transform: scale(2);
  }
  .mobile-menu button {
    width: 90%;
    margin: 1rem;
  }
}
