.main-wrapper {
  width: 100%;
  height: 87vh;
  color: white;
}
.overlay {
  background-color: rgba(0, 0, 0, 0.3);
  position: absolute;
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
}
#video {
  width: 100%;
  height: 87vh;
  object-fit: cover;
  z-index: -1;
}
.main-wrapper .main-info {
  position: absolute;
  top: 0;
  height: 100vh;
  display: flex;
  justify-content: center;
  flex-direction: column;
  text-align: center;
  width: 100%;
  margin: 3rem 0;
  padding: 2rem 0;
}
.main-info h1 {
  font-size: 40px;
  color: rgb(225, 169, 66);
  text-transform: uppercase;
  font-weight: 500;
}
.main-info h3 {
  font-size: 28px;
  color: #fff;
  font-weight: 400;
}
.main-info button {
  align-items: center;
  float: left;
  padding: auto;
  margin: 1rem auto;
  background: transparent;
  font-weight: 600;
}
.main-info button:hover {
  background: linear-gradient(
    45deg,
    var(--primary-dark) 0%,
    var(--primary-light) 100%
  );
}
.bottom-btn a {
  color: #fff;
  text-transform: uppercase;
}
#tsparticles {
  height: 87vh;
  position: absolute;
  width: 100%;
}

@media screen and (max-width: 940px) {
  .main-info h1 {
    font-size: 25px;
  }
  .main-info h3 {
    font-size: 18px;
  }
  .overlay {
    height: 87vh;
  }
}
