.skills {
  width: 100%;
  height: 100%;
  text-align: center;
  justify-content: center;
  align-items: center;
  margin: 1rem 0;
}
.header {
  width: 100%;
  background-color: rgb(233, 233, 225);
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 1rem 0;
}
.header h3 {
  text-transform: uppercase;
  font-size: 25px;
  color: rgb(225, 169, 66);
}
.s-images {
  --num-cols: 4;

  width: 100%;
  display: grid;
  text-align: center;
  grid-template-columns: repeat(var(--num-cols), 1fr);
  padding: 2rem 0;
  padding-left: 2rem;
  gap: 1rem 0;
}
.s-imgs {
  width: 250px;
}
.s-imgs img {
  width: 40%;
  margin: auto;
  padding-top: 1rem;
}

.s-imgs:hover {
  transition: 0.2s;
  transform: scale(0.9);
}
@media screen and (max-width: 940px) {
  .s-images {
    --num-cols: 2;
    --row-height: 100px;
    justify-content: center;
    align-items: center;
    text-align: center;

    grid-template-columns: repeat(var(--num-cols), 1fr);
  }
  .s-imgs {
    width: 150px;
  }
}
