.about {
  width: 100%;
  padding: 4rem 0;
}
.about .container {
  display: grid;
  grid-template-columns: 1fr 2fr;
  grid-gap: 2rem;
  padding: 0 1rem;
}
.container .right {
  font-family: "Poppins", sans-serif;
}
.left img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.right h2 {
  color: rgb(225, 169, 66);
}
.social-links {
  display: flex;
  align-items: center;
  margin-top: 3rem;
}
.social-links a {
  width: 40px;
  height: 40px;
  text-align: center;
  text-decoration: none;
  color: black;
  box-shadow: 0 0 20px 10px rgba(0, 0, 0, 0.05);
  margin: 0 10px;
  border-radius: 50%;
  position: relative;
  overflow: hidden;
  transition: transform 0.5s;
}
.social-links a .icon {
  font-size: 13px;
  height: 40px;
  position: relative;
  z-index: 10;
  transition: color 0.5s;
}
.social-links a::after {
  content: "";
  width: 100%;
  height: 100%;
  top: -90px;
  left: 0;
  background: linear-gradient(-45deg, #ed1c94, #ffec17);
  position: absolute;
  transition: 0.5s;
}
.social-links a:hover::after {
  top: 0;
}
.social-links a .icon:hover {
  color: #fff;
}
.social-links a:hover {
  transform: translateY(-10px);
}

@media screen and (max-width: 940px) {
  .about .container {
    grid-template-columns: 1fr;
  }

  .social-links {
    display: none;
  }
}
